<template>
  <div class="container">
    <h1>Stati nella tabella coda</h1>
    <hr />
    <div class="row">
      <div class="col-md-4">
        <div class="form-group">
          <label for="">Data Inizio</label>
          <input
            type="date"
            class="form-control"
            :v-model="startDate"
            :value="startDate"
            @input="startDate = getDate($event.target.valueAsDate, 0)"
          />
        </div>
      </div>
      <div class="col-md-4">
        <div class="form-group">
          <label for="">Data Fine</label>
          <input
            type="date"
            class="form-control"
            :v-model="endDate"
            :value="endDate"
            @input="endDate = getDate($event.target.valueAsDate, 0)"
          />
        </div>
      </div>
      <div class="col-md-4"></div>
      <div class="col-md-4">
        <div class="form-group">
          <button @click="submit" class="btn btn-success">Aggiorna</button>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-md-10 col-sm-12">
        <table class="table">
          <thead>
            <tr>
              <th>Stato</th>
              <th>Quantita</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in data" :key="index">
              <td>{{ item.status }}</td>
              <td>{{ item.quantity }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      data: [],
      startDate: null,
      endDate: null,
    };
  },
  mounted() {
    var date = new Date();
    this.endDate = this.getDate(date, 0);
    this.startDate = this.getDate(date, -7);
    // console.log(this.startDate);
    // console.log(this.endDate);
    this.submit();
  },
  methods: {
    getDate(date, day) {
      date.setDate(date.getDate() + day);
      let year = date.getFullYear();
      let month =
        (date.getMonth() + 1) < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      let dd =
        date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      return year + "-" + month + "-" + dd;
    },
    submit() {
      var link =
        "https://archicons.archivium-srl.it/ArchiHub/procedures/varie/retention/api.php?type=statusCoda&start_date=" +
        this.startDate +
        "&end_date=" +
        this.endDate;
      this.$store.dispatch("loading");
      this.axios.get(link).then((r) => {
        this.data = r.data;
        this.$store.dispatch("loaded");
      });
    },
  },
};
</script>